.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    z-index: 1000;
    cursor: pointer;
}
.header-bg {
    background-color: black;
    position: sticky;
    top: 0;
}
.header h1{
    color:white;
}
.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1.3rem;
}

.nav-menu li:hover {
    transform: scale(1.1);
    background-color: var(--main-color);
    border-radius: 5px;
    transition: 0.3s;
}

.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
    color: white;
}

.nav-bar {
    display: none;
}

@media screen and (max-width:992px) {
    .nav-bar {
        display: flex;
        align-items: center;
    }

    .nav-menu {
        flex-direction: column;
        width: 100%;
        height: 100vh;
        position: fixed;
        justify-content: center;
        align-items: center;
        top: 10px;
        left: -100%;
        text-align: center;
        z-index: -1;
        transition: 0.3s;
        background-color: black;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 0.8rem 1.4rem;
        border-radius: 10px;
        width: 100%;
    }

    .nav-menu a {
        font-size: 2rem;
    }

    .nav-menu li:hover {
        background-color: #3586ff;
    }
}
