.text {
    position: relative;
    font-size: 8vw;
    top: 50px;
    text-align: center;
    height: 200px;
}
.text span {
    font-size: 7vw;
}
.desc h2{
    color: blue;
    font-size: 2.5rem;
    margin: 30px 0;
}
.desc p {
    line-height: 1.8;
}
.desc {
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
}
/*parallax image*/
.sec1,.sec2,.sec3 {
    width: 100%;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
}
.sec1 {
    background-image: url(./images/parallax1.jpg);
    position: relative;
    height: 100vh;
}
.sec1::before{
    content:"";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}
.sec2 {
    background-image: url(./images/parallax2.jpg);
    height: 50vh;
}
.sec3 {
    background-image: url(./images/parallax3.jpg);
    height: 50vh;
}
.container {
    margin-left: 30px;
    margin-right: 30px;
}
.services {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    padding: 20px 0;
}
@media screen and (max-width:992px) {
    .services {
        grid-template-columns: 1fr 1fr;
    }
}
@media screen and (max-width:768px) {
    .services {
        grid-template-columns: 1fr;
    }
}
.landing {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: white;
    width: 100%;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.landing h1 {
    font-size: 100px;  
    position: relative;
    z-index: 1;
    margin-bottom: 30px;
}
.landing span {
    -webkit-mix-blend-mode:difference;
    -moz-mix-blend-mode:difference;
    -o-mix-blend-mode: difference;
    mix-blend-mode: difference;
}
.landing h1::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100%;
    background-color: rgb(250, 245, 245);
    z-index: -1;
    animation: animatHead 4s linear infinite;
    border-radius: 5px;
}
@keyframes animatHead {
    0%,100%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(320px);
    }
}
 @media screen and (max-width:460px) {
    .landing h1 {
        font-size: 50px;
    }
    @keyframes animatHead {
    
        0%,
        100% {
            transform: translateX(0);
        }
    
        50% {
            transform: translateX(180px);
        }
    }
    .landing h1::before {
        width: 30px;
    }
}
 @media screen and (max-width:250px) {
    .landing h1 {
        font-size: 30px;
    }
    @keyframes animatHead {
    
        0%,
        100% {
            transform: translateX(0);
        }
    
        50% {
            transform: translateX(100px);
        }
    }
    .landing h1::before {
        width: 30px;
    }
}
.landing p {
    line-height: 1.8;
    text-align: center;
}
.box {
